<h1 id="多客服消息转发" tabindex="-1"><a class="header-anchor" href="#多客服消息转发" aria-hidden="true">#</a> 多客服消息转发</h1>
<p>多客服的消息转发绝对是超级的简单，转发的消息类型为 <code>transfer</code>：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code>
<span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Kernel<span class="token punctuation">\</span>Messages<span class="token punctuation">\</span>Transfer</span><span class="token punctuation">;</span>

<span class="token comment">// 转发收到的消息给客服</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">server</span><span class="token operator">-></span><span class="token function">push</span><span class="token punctuation">(</span><span class="token keyword">function</span><span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
  <span class="token keyword">return</span> <span class="token keyword">new</span> <span class="token class-name">Transfer</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$response</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">server</span><span class="token operator">-></span><span class="token function">serve</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br></div></div><p>当然，你也可以指定转发给某一个客服：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Kernel<span class="token punctuation">\</span>Messages<span class="token punctuation">\</span>Transfer</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">server</span><span class="token operator">-></span><span class="token function">push</span><span class="token punctuation">(</span><span class="token keyword">function</span><span class="token punctuation">(</span><span class="token variable">$message</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
    <span class="token keyword">return</span> <span class="token keyword">new</span> <span class="token class-name">Transfer</span><span class="token punctuation">(</span><span class="token variable">$account</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div>